import React, {Component} from 'react'
import classes from './Button.module.css'

const Button = props => {
    const cls = [
        classes.Button,
        classes[props.type]
    ]
    return (
        <button
            onClick={props.onClick}
            disabled={props.disabled}
            className={cls.join(' ')}
        >
            {props.children}
        </button>
    )
}

export default Button


